import Vue from "vue";
import VueRouter from "vue-router";
import integrate from "../components/integrate.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "integrate",
    component: integrate,
    redirect: "/economics",

    children: [
      {
        path: "/economics",
        component: () => import("../views/Economics/economics.vue"),
      },
      {
        path: "/Biography",
        component: () => import("../views/Biography/Biography.vue"),
      },
      {
        path: "/History",
        component: () => import("../views/History/History.vue"),
      }, {
        path: "/Literature",
        component: () => import("../views/Literature/Literature.vue"),
      }, {
        path: "/MedicalScience",
        component: () => import("../views/MedicalScience/MedicalScience.vue"),
      }, {
        path: "/Metaphysics",
        component: () => import("../views/Metaphysics/Metaphysics.vue"),
      }, {
        path: "/Novel",
        component: () => import("../views/Novel/Novel.vue"),
      }, {
        path: "/Philosophy",
        component: () => import("../views/Philosophy/Philosophy.vue"),
      },{
        path: "/Psychology",
        component: () => import("../views/Psychology/Psychology.vue"),
      },
    ],
  },
];

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
